.price {
  margin: 30px 0 55px;

  & h1 {
    font-size: 36px;
    margin-bottom: 20px;
    text-align: center;
  }

  & .form-check {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 30px;
    font-weight: 600;
    padding-left: 0;
  }

  & .card-title, .h6 {
    font-weight: 700;
    margin-bottom: 0;
  }

  & .card-title {
    font-size: 24px;
  }

  & .h6 {
    font-size: 20px;
  }

  & .card {
    border-radius: 15px;
    max-width: 280px;
    margin: 0 auto;
    border: 1px solid #EBEBEB;
    box-sizing: border-box;
    box-shadow: 0px 3px 12px rgba(0, 0, 0, 0.12);
    padding: 30px;

    & .btn {
      font-size: 16px;
      margin-top: 10px;
    }
  }
}

.price__text {
  max-width: 550px;
  font-size: 20px;
  margin: 0 auto 50px;
}

.price__light {
  font-weight: 300;
  font-size: 16px;
}

#flexSwitchCheckDefault.price__toggle {
  margin: 0 10px;
  width: 60px;
  height: 30px;
  border-color: #ccc;
  background-color: #fff;
  background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='-4 -4 8 8'%3e%3ccircle r='3' fill='%23147AA9'/%3e%3c/svg%3e");

  &:focus {
    box-shadow: 0px 3px 12px rgba(0, 0, 0, 0.12);
    border-color: #ccc;
    background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='-4 -4 8 8'%3e%3ccircle r='3' fill='%23147AA9'/%3e%3c/svg%3e");
  }

  &:checked {
    border-color: #ccc;
    background-color: #fff;
    background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='-4 -4 8 8'%3e%3ccircle r='3' fill='%23147AA9'/%3e%3c/svg%3e");
  }
}

@media (max-width: 768px) {
  .price h1 {
    font-size: 36px;
  }
}
